export default (_self, h) => {
  return [
    h(
      "el-select", {
        props: {
          placeholder: _self.obj.placeholder || "",
          value: _self.obj.value || ''
        },
        on: {
          'input' (value) {
            let obj = _self.obj.items.find((item)=>{
              console.log(item)
               return item.label_value == value;
            });
            value = obj.label_name
            // if (!_self.obj.name) {
            //   return false;
            // }
            _self.obj = Object.assign(_self.obj, {
              value
            });
            _self.$emit('handleChangeVal', value)
          }
        }
      },
      _self.obj.items.map(v => {
        return h(
          "el-option", {
            props: {
              value: "" + v.label_value
            },
          },
          v.label_name
        );
      })
    )
  ];
};

export let selectConf = {
  // 
  type: 'select',
  // 
  config: true,
  // label
  label: '',
  placeholder: '',
  // 
  inlineBlock: false,
  // 
  require: true,
  // 
  items: Array.apply(null, { length: 5 })
    .map((k, v) => {
      return {
        label_value: v + 1,
        label_name: "" + (v + 1),
      }
    }),
  // 
  value: '',
  // name
  name: '',
  // 
  ruleError: '',
  // 
  relation: false,
  // name
  relation_name: '',
  // value
  relation_value: '',
  // 
  visibility: true
}
