export default (_self, h) => {
  return [
    h("el-radio-group", {
      props: {
        value: _self.obj.value || "1"
      },
      on: {
        'input' (value) {
          console.log(value)
          // if (!_self.obj.name) {
          //   return false;
          // }
          console.log(_self.obj)
          _self.obj = Object.assign(_self.obj, {
            value
          });
          console.log("xxxxxxx",_self.obj)
          _self.$emit('handleChangeVal', value)
        }
      }
    }, _self.obj.items.map(v => {
      return h("el-radio", {
        props: {
          label: v.label_value
        },
      }, v.label_name)
    }))
  ];
};

