import area from '../../config/area'
export default (_self, h) => {
  let control = [
    h('el-cascader', {
      style: {
        width: _self.obj.details_address ? '200px' : '100%',
        display: 'inline-block'
      },
      props: {
        placeholder: _self.obj.placeholder || (_self.obj.name ? "" : ""),
        options: area,
        "v-model": _self.obj.value || [],
        filterable: false,
        'expand-change': true,
        // trigger: "hover"
      },
      change: {
        "change" (arr) {
          console.log("valvalval",arr)
          // if (!_self.obj.name) {
          //   return false;
          // }
          _self.obj.value = arr;
          _self.$emit('handleChangeVal', arr);
        }
      }
    }),
    h("Input", {
      props: {
        placeholder: _self.obj.placeholder || "",
        ref: 'details_address',
        value: (_self.obj.value[3] || {})
          .name
      },
      style: {
        width: 'auto',
        display: !_self.obj.name ? 'none' : 'inline-block',
        'margin-left': '5px',
        'min-width': '300px'
      },
      on: {
        "change": function(val) {
          console.log("valvalval",val)
          // if (!_self.obj.name) {
            // return false;
          // }
          let temp_data = _self.obj.value.slice(0, 3);
          _self.obj.value = temp_data.concat(val.currentTarget.value);
          _self.$emit('handleChangeVal', _self.obj.value)
        }
      }
    })
  ];

  if (!_self.obj.details_address) {
    control.splice(1, 1);
  }
  return control;
}
