export default (_self, h) => {
  return [
    h("el-input", {
      props: {
        value: _self.obj.value || ""
      },
      attrs: {
        maxlength: parseInt(_self.obj.maxlength) || 20,
        placeholder: _self.obj.placeholder || "",
      },
      on: {
        // eslint-disable-next-line no-unused-vars
        "input": function(val) {
          console.log("valuevaluevalue", event.currentTarget.value)
          //   if (!_self.obj.name) {
          //     return false;
          //   }
          _self.obj.value = event.currentTarget.value;
          _self.$emit('handleChangeVal', event.currentTarget.value)
        }
      }
    })
  ];
};


export let inputConf = {
  // 
  type: 'input',
  // 
  config: true,
  // label
  label: '',
  name: '',
  placeholder: '',
  // 
  maxlength: 20,
  value: '',
}
